@use '../abstracts/' as *;

@mixin frontend-mentor-trick() {
  &::before {
    --width: 375px;
    --height: 667px;

    content: '';
    position: absolute;
    opacity: 0.7;
    width: var(--width);
    height: var(--height);
    background-image: url(../design/mobile-design.jpg);
    background-repeat: no-repeat;
    background-size: var(--width) var(--height);
    z-index: -1;

    @include media-breakpoint('desktop') {
      --width: 1440px;
      --height: 900px;

      background-image: url(../design/desktop-design.jpg);
      background-size: cover;
    }
  }
}

body {
  position: relative;
  background-color: var(--clr-dark-blue-main);

  @include frontend-mentor-trick();
}
